.bg {
  margin-left: auto;
  margin-right: auto;
  padding-top: 118px;
  padding-bottom: 118px;

  max-width: 480px;
  max-height: 400px;

  background-image: linear-gradient(
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    /* url(../../shared/images/hero/hero_example_1x.jpg); */
      url(../../shared/images/hero/hero_test3.jpeg);
  background-color: var(--black-text-color);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contentWrapper {
  padding: 0;
  text-align: center;

  /* gsap */
  opacity: 0;
  /*  */
}

.mainTitle {
  margin-left: auto;
  margin-right: auto;
  text-align: center;

  color: snow;
  /* letter-spacing: 0.06em; */
}
