.heroBtn {
  margin-top: 30px;
  padding: 10px 32px;
  margin-top: 20px;
}

@media screen and (min-width: 768px) {
  .bg {
    max-width: 768px;
    max-height: 500px;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          rgba(47, 48, 58, 0.4),
          rgba(47, 48, 58, 0.4)
        ),
        /* url(../../shared/images/hero/hero_example_1x.jpg); */
          url(../../shared/images/hero/hero_test3.jpeg);
    }
  }

  .mainTitle,
  .accentTitle {
    width: 360px;
  }

  .heroBtn {
    margin-top: 50px;
  }
}

@media screen and (min-width: 1200px) {
  .bg {
    max-width: 1600px;
    height: 832px;
    /* max-height: 600px; */
    padding-top: 200px;
    padding-bottom: 200px;

    background-image: linear-gradient(
        rgba(47, 48, 58, 0.4),
        rgba(47, 48, 58, 0.4)
      ),
      url(../../shared/images/hero/hero_test3.jpeg);
    /* url(../../shared/images/hero/hero_example_1x.jpg); */

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          rgba(47, 48, 58, 0.4),
          rgba(47, 48, 58, 0.4)
        ),
        /* url(../../shared/images/hero/hero_example_1x.jpg); */
          url(../../shared/images/hero/hero_test3.jpeg);
    }
  }

  .mainTitle,
  .accentTitle {
    /* width: 696px; */
    margin: 0 auto;
    position: relative;
    width: 876px;
    left: 32px;
  }
  .heroBtn {
    font-size: 40px;
  }
}

@media screen and (min-width: 1600px) {
  .bg {
    max-width: 1920px;
    max-height: 832px;
  }
}
